.list-item{height:36px;padding:20px 0 20px 0;position:relative}.list-item .info{height:100%;float:left}.list-item .info .start,.list-item .info .contents{white-space:nowrap}.list-item .info .start{width:100%;font-size:16px;color:rgba(0,0,0,0.87)}.list-item .info .contents{width:100%;font-size:14px;color:rgba(0,0,0,0.54)}.list-item .location{color:rgba(0,0,0,0.38);position:absolute;right:16px;top:20px;font-size:12px}.icon{width:40px;height:100%;padding:0 16px 0 16px;position:relative;float:left}.icon .bar-top,.icon .bar-bottom{width:4px;height:26px;position:absolute;left:calc(50% - 2px)}.icon .bar-top{top:-20px}.icon .bar-bottom{bottom:-20px}.icon .material-icons{font-size:18px;position:absolute;top:calc(50% - 9px);left:calc(50% - 9px)}



