body{margin:0;font-family:'Roboto', sans-serif}ul{list-style:none}.header{width:100%;height:64px;background-color:#43A047;color:#ffffff;position:fixed;line-height:64px;box-shadow:0 3px 12px rgba(0,0,0,0.23);top:0;z-index:99}@media only screen and (max-width: 768px){.header{height:56px}}.header .title{float:left;font-size:18px;line-height:64px}.header .back{float:left;cursor:pointer;margin:0 32px 0 24px;font-size:24px;height:64px;width:24px}.header .back i{line-height:64px}.page{padding:64px 0 0 0}@media only screen and (max-width: 768px){.page{padding:64px 0 0 0}}



